@import url(https://fonts.googleapis.com/css?family=Lato:400,300,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    font: 14px/22px "Lato", Arial, sans-serif;
    margin-top: 55px;
    background: linear-gradient(110deg, #979797 55%, #bfbfbfe8 55%);
    min-height: calc(100vh - 55px);
}

.cart-logo{
    position: absolute;
    top: 0px;
    font-size: 14px;
    background-color: darkred;
    padding: 0px 5px;
    right: -0.4rem;
    border-radius: 50%;
}

.transform-it{
    transform: skewY(-5.2deg);
}
.card-cart{box-shadow:0 10px 100px rgba(0,0,0,0.2);margin:20px 0;background-color:white;position:relative}.card-cart .remove-btn{z-index:1;position:absolute;right:0;top:0;background:#d6e2f6;border:0;color:red;font-weight:600;font-size:15px;padding:0px 5px;border-radius:0;text-transform:capitalize;box-shadow:none}.card-cart .card-image{background-color:#d6e2f6}.card-cart .card-image img{height:150px;width:100%;object-fit:cover;padding:20px 0}.card-cart .card-info .card-title{font-weight:600;padding:10px 0 5px 0;color:#2e2f63;margin-bottom:0}.card-cart .card-info .card-meta{padding:0;font-size:13px;color:#838485}.card-cart .card-info .card-meta li{display:inline-block;text-transform:capitalize;padding-right:10px}.card-cart .card-info .qi-wrapper{padding-top:10px}.card-cart .card-info .qi-wrapper .quantity{border:2px solid #9ea0d9;border-radius:3px;color:#2e2f63;font-size:14px;font-weight:600;padding:0 0 0 10px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.card-cart .card-info .qi-wrapper .quantity button{background:none;border:0;color:#2e2f63;font-size:20px;padding:0 10px;box-shadow:none}.card-cart .card-info .qi-wrapper .price{color:#2e2f63;font-size:19px;font-weight:700;padding:3px 35px 0 0;text-align:right}

.order-summary{color:#1e1b4d;text-align:right}.order-summary .title{font-size:80px;font-weight:200;line-height:1.15}.order-summary .total{font-size:40px;font-weight:500;line-height:1.15;margin-top:60px}

.remove-btn{position:absolute;right:-15px;top:-15px;background:white;border:0;color:gray;font-size:24px;padding:0px 12px;border-radius:50%;box-shadow:2px 2px 25px #000;z-index:1}.movie-card{font:14px/22px "Lato", Arial, sans-serif;color:#a9a8a3}.movie-card .hero{height:342px;margin:0;position:relative;overflow:hidden;z-index:1;border-top-left-radius:5px;border-top-right-radius:5px}.movie-card .hero .details{padding:170px 0 0 265px}.movie-card .hero .details .title1{color:white;font-size:44px;margin-bottom:13px;position:relative;line-height:1;max-width:80vh}.movie-card .hero .details .title1 span{position:absolute;top:-10px;margin-left:0px;background:#c4af3d;border-radius:5px;color:#544c21;font-size:14px;padding:4px 7px}.movie-card .hero .details .title2{color:#c7c1ba;font-size:23px;font-weight:300;margin-bottom:15px}.movie-card .hero .details .likes{margin-left:24px}.movie-card .hero:before{content:"";width:100%;height:100%;position:absolute;overflow:hidden;top:0;left:0;z-index:-1;background:url("https://storage.needpix.com/rsynced_images/camera-736193_1280.jpg");transform:skewY(-2.2deg);transform-origin:0 0;-webkit-backface-visibility:hidden}.cover{position:absolute;top:180px;left:40px;z-index:2;width:200px;max-height:300px}.description{bottom:0px;height:200px;font-size:16px;line-height:26px;color:#b1b0ac}.description .column1{padding-left:50px;padding-top:120px;width:220px;float:left;text-align:center}.description .column1 .tag{background:white;border-radius:10px;padding:3px 8px;font-size:14px;margin-right:4px;line-height:35px;cursor:pointer}.description .column1 .tag:hover{background:#ddd}.description .column2{padding-left:35px;padding-top:30px;margin-left:20px;width:480px;float:left}

